@import "variables";


#aldi-nav-container {
    margin: 1.5em 0 .25em 0;
    padding: 0 1em;
    text-align: center;

    @include for-tablet-landscape-up{
        //text-align: right;
    }

    ko-navigation{
        display: inline-block;
        margin-bottom: .75em;
    }

    .aldi-navlink,
    .aldi-navlink-disabled,
    .aldi-curlink{
        display: inline-block;
        line-height: 32px;
        height: 32px;
        text-align: center;
        margin: 0 1em;
        vertical-align: top;
    }

    .aldi-curlink{
        border: 1px solid transparent;
        
        @extend .normal-font-size;
    }

    .aldi-navlink{
        border: 1px solid $ws_medgrey;
        border-radius: 4px;
        text-decoration: none;
        color: $ws_medgrey;
        width: 30px;
        cursor: pointer;

        &:hover{
            background-color: $ws_lightgrey;
        }
    }

    .aldi-navlink.aldi-navlink-disabled{
        opacity: .7;
        cursor: default;
    }

    .aldi-navlink i:before{
        line-height: 36px;
        font-size: 18px;
        text-align: center;
    }

    .aldi-links{
        margin-top: -16px;
        // vertical-align: bottom;
        display: block;
        text-align: right;
        // padding-top: 26px;

        @extend .small-font-size;
    }
}