@import "variables";
@import "base";
@import "fonts";
@import "footer";
@import "header";
@import "table";
@import "navigation";

// Future Configurable

body{
    background-color: $color-background-grey;
    color: $text-color-normal;
}
#aldi-header-container{
    border-bottom: 1px solid $ws_lightgrey;
    background-color: $color-background-white;
}

#aldi-table-container{
    background-color: $color-background-white;
    border: 1px solid $ws_lightgrey;
}

#aldi-banner{
    text-align: right;
    color: $text-color-light;
    padding-right: 2em;
    
    @extend .small-font-size;

    a {
        text-decoration: none;
        color: $text-color-light;
    }
}

#aldi-footer{
    background-color: $color-background-dark;
    color: $ws_lightgrey;
}


// non-configurable

html, body{
    height: 100%;    // sticky footer
}
body{
   margin: 0;
   padding: 0;

   // sticky footer
   display: flex;
   flex-direction: column;
}

.aldi-text-light{
    color: $text-color-light;
}

#aldi-container{
    margin: 0;
    padding-bottom: 2em;
    flex: 1 0 auto; // sticky footer

    @include for-tablet-landscape-up {
        min-width: 800px;
        max-width: 100%;
        margin: 0 auto;
    }

    @include for-desktop-up {
        min-width: 1000px;
        max-width: 90%;
    }
}

#aldi-table-container{
    position: relative;
    margin: .5em;
    padding: .5em;
    overflow-x: auto;
}

#aldi-footer{
    flex-shrink: 0; // sticky footer
}
