@import "variables";

.aldi-header{
    padding: 1em 0;

    @include for-tablet-portrait-up{
        padding: 1em 2em;
    }
}

.aldi-header-main{
    line-height: 60px;
    overflow-y: hidden;
    text-align: center;

    @include for-tablet-portrait-up{
        float: left;
    }
}

.aldi-logo{
    height: 40px;
    padding: 10px;
    width: 40px;
    text-align: center;
    display: inline-block;
    vertical-align: top;

    img{
        height: 40px;
        max-width: 40px;
    }

    @include for-tablet-portrait-up{
        text-align: left;
    }
}

.aldi-title{
    display: inline-block;
    margin: 0px;
    
    @extend h3;
}

.aldi-header-secondary{
    text-align: center;
    line-height: 20px;

    a{
        margin: 0 0.5em;
        color: $text-color-light;
    }

    a:hover{
        text-decoration: none;
    }

    @include for-tablet-portrait-up{
        float: right;
        height: 60px;
        line-height: 60px;
    }
}